@font-face {
	font-family: 'NotoSans';
	src: url(/static/media/NotoSansCJKkr-Regular.bc433f24.woff);
	font-weight: 400;
}

@font-face {
font-family: 'NotoSans';
src: url(/static/media/NotoSansCJKkr-Medium.183ca1fa.woff);
font-weight: 500;
}

@font-face {
font-family: 'NotoSans';
src: url(/static/media/NotoSansCJKkr-Bold.e708e528.woff);
font-weight: 700;
}

@font-face {
font-family: 'NotoSans';
src: url(/static/media/NotoSansCJKkr-Black.c1e57273.woff);
font-weight: 900;
